import { WalletMultiButton } from '@solana/wallet-adapter-react-ui';
import './App.css';
import { useEffect } from 'react';

const telegram = (window as any).Telegram?.WebApp;

function App() {
  useEffect(() => {
    telegram.ready();
  }, []);

  const toPhantom = () => {
    (window as any).location = "phantom-crypto-wallet://myparam";
  }

  return (
    <div className="App">
      <WalletMultiButton></WalletMultiButton>
      <button onClick={() => toPhantom()}></button>
    </div>
  );
}

export default App;
